html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

@font-face {
  font-family: "Bebas";
  src: url("../fonts/BebasNeuewebfont.woff2") format("woff2"), url("../fonts/BebasNeuewebfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "HelveticaNeue";
  src: url("../fonts/HelveticaNeue.woff2") format("woff2"), url("../fonts/HelveticaNeue.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  background: #FFF;
  font-family: HelveticaNeue;
  font-size: 12px;
}

.out {
  overflow: hidden;
}

.container {
  max-width: 940px;
  margin: 0 auto;
}

@media only screen and (max-width: 900px) {
  .container {
    margin: 0 10px;
  }
}

h1 {
  font-family: Bebas, sans-serif;
  color: #e8603c;
  font-size: 18px;
  font-weight: 400;
  line-height: 18px;
}

.title {
  color: #e8603c;
}

a {
  text-decoration: none;
}

.nav {
  width: 100%;
  overflow: hidden;
  *zoom: 1;
}

.nav:after {
  content: " ";
  display: table;
  clear: both;
}

.nav__logo {
  float: left;
  margin: 22px 0 13px 0;
  font-family: Bebas, sans-serif;
  color: #e8603c;
  font-size: 36px;
  font-weight: 400;
}

.nav__logo span {
  color: #f3af9d;
}

.nav__logo a:visited {
  color: #e8603c;
}

.nav__list {
  float: right;
}

@media only screen and (max-width: 500px) {
  .nav__list {
    padding-bottom: 30px;
    width: 100%;
  }
}

.nav__litem {
  display: block;
  float: left;
  font-family: Bebas, sans-serif;
  color: #b9b9b9;
  font-size: 16px;
  font-weight: 400;
  padding-left: 28px;
  line-height: 16px;
  margin-top: 37px;
}

@media only screen and (max-width: 500px) {
  .nav__litem {
    float: none;
    text-align: center;
  }
}

.nav__litem a {
  color: #b9b9b9;
}

.nav__litem--active a {
  color: #e8603c;
}

.header {
  position: relative;
  width: 100%;
  height: 61px;
  background-color: #e8603c;
}

.header__title {
  position: absolute;
  font-family: Bebas, sans-serif;
  color: #ffffff;
  font-size: 24px;
  font-weight: 400;
  line-height: 1.5;
  margin: 13px 0 0 19px;
}

.bgc:before {
  content: '';
  position: absolute;
  height: 100%;
  width: 50%;
  top: 0;
  left: calc( -50% - 10px);
  background-color: #f3af9d;
}

.bgc:after {
  content: '';
  position: absolute;
  height: 100%;
  width: 50%;
  top: 0;
  right: calc( -50% - 10px);
  background-color: #f3af9d;
}

main {
  *zoom: 1;
}

main:after {
  content: " ";
  display: table;
  clear: both;
}

aside {
  float: right;
  width: 25%;
  padding-top: 28px;
}

@media only screen and (max-width: 500px) {
  aside {
    width: 100%;
  }
}

.categories__title {
  font-family: Bebas, sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #e8603c;
  margin-bottom: 21px;
  background: url(../img/Line.png) 100% repeat-x;
}

.categories__title span {
  background-color: white;
  padding-right: 17px;
}

.categories__text {
  margin-bottom: 37px;
}

.categories__litem {
  color: #464646;
}

@media only screen and (max-width: 500px) {
  .categories {
    text-align: center;
  }
  .categories li {
    margin-bottom: 10px;
  }
}

.about__title {
  font-family: Bebas, sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #e8603c;
  margin-bottom: 21px;
  background: url(../img/Line.png) 100% repeat-x;
}

.about__title--white {
  color: #ffffff;
  font-size: 20px;
  margin-bottom: 27px;
  background: none;
}

.about__title span {
  background-color: white;
  padding-right: 17px;
}

.about__text {
  margin-bottom: 37px;
}

.about__text p {
  margin-bottom: 22px;
}

.gallery__title {
  font-family: Bebas, sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #e8603c;
  margin-bottom: 21px;
  background: url(../img/Line.png) 100% repeat-x;
}

.gallery__title span {
  background-color: white;
  padding-right: 17px;
}

.gallery__board {
  margin-top: 19px;
  margin-bottom: 30px;
  *zoom: 1;
}

.gallery__board:after {
  content: " ";
  display: table;
  clear: both;
}

.gallery__img {
  width: calc(25% - 6px);
  float: left;
  margin: 0 6px 6px 0;
}

.gallery img {
  width: 100%;
  border: 1px solid #e8603c;
}

.content {
  padding-top: 28px;
  width: calc(75% - 50px);
  float: left;
}

@media only screen and (max-width: 500px) {
  .content {
    width: 100%;
  }
}

.post {
  position: relative;
  *zoom: 1;
}

.post:after {
  content: " ";
  display: table;
  clear: both;
}

.post__icon {
  position: absolute;
  width: 75px;
  height: 75px;
  background-color: #e8603c;
  left: -85px;
}

.post__icon img {
  position: absolute;
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.post__img {
  width: 100%;
}

.post__img img {
  width: 100%;
}

.post__meta {
  padding-top: 20px;
  width: 20%;
  float: left;
}

.post__summary {
  padding-top: 20px;
  width: 80%;
  float: left;
}

.post__borderb {
  margin-top: 40px;
  margin-bottom: 46px;
  float: left;
  height: 14px;
  width: calc(100% - 7px);
  background: url(../img/Line.png) 100%;
}

.slider {
  max-width: 100%;
  position: relative;
  overflow: hidden;
}

.slider__next, .slider__prev {
  position: absolute;
  top: calc(50% - 26px);
  width: 36px;
  height: 52px;
  margin: 0 18px;
  cursor: pointer;
}

@media only screen and (max-width: 500px) {
  .slider__next, .slider__prev {
    display: none;
  }
}

.slider__next {
  right: 0;
  background-image: url(../img/next.png);
}

.slider__prev {
  left: 0;
  background-image: url(../img/previous.png);
}

.data {
  padding-bottom: 12px;
}

.data a {
  color: #bababa;
}

.data__title {
  font-family: Bebas, sans-serif;
  line-height: 1.5;
  color: #e8603c;
}

.data__value {
  color: #bababa;
  line-height: 1.5;
}

.postdescr {
  width: calc(100% - 21px);
}

.postdescr__title {
  font-size: 18px;
  font-family: Bebas, sans-serif;
  color: #e8603c;
  font-weight: 400;
}

.postdescr__title a {
  color: #E8603C;
}

.postdescr__text {
  margin-top: 20px;
  color: #414141;
  line-height: 1.5;
}

.postdescr__text a {
  color: #414141;
}

.postdescr__text p {
  margin-bottom: 20px;
}

.postdescr__adress {
  color: #A0A0A0;
  font-weight: 400;
  line-height: 1.5;
}

.quotation {
  width: calc(100% - 16px);
  margin-right: 16px;
  background-color: #F5F5F5;
}

.quotation__text {
  padding: 25px 30px 20px  75px;
  line-height: 1.4;
  font-style: italic;
}

.quotation span {
  color: #E8603C;
  font-size: 72px;
  font-family: Georgia;
  line-height: 1.5;
  font-weight: 400;
  float: left;
  margin-left: 32px;
}

.pagination {
  padding-bottom: 41px;
  *zoom: 1;
}

.pagination:after {
  content: " ";
  display: table;
  clear: both;
}

.pagination__page {
  display: block;
  width: 26px;
  height: 26px;
  background-color: #f3b29d;
  float: left;
  font-family: Bebas, sans-serif;
  color: white;
  font-size: 18px;
  line-height: 1.5;
  margin-right: 4px;
  text-align: center;
}

.pagination__page--active {
  background-color: #e8663c;
}

.footer {
  position: relative;
  background-color: #e8663c;
  width: 100%;
  color: #ffffff;
  font-size: 12px;
}

.footer a {
  color: #f3b29d;
  font-weight: 300;
}

.footer__about {
  margin: 38px 0 0 35px;
  width: calc(32% - 35px);
  float: left;
}

.footer__widget {
  margin: 38px 0 0 64px;
  width: calc(30% - 64px);
  float: left;
}

.wrapper {
  *zoom: 1;
}

.wrapper:after {
  content: " ";
  display: table;
  clear: both;
}

.widget__title {
  font-family: Bebas, sans-serif;
  color: #ffffff;
  font-size: 20px;
  font-weight: 400;
  text-align: left;
  margin-bottom: 27px;
}

.widget__block {
  margin-bottom: 19px;
}

.widget__bltext {
  margin-bottom: 8px;
}

/* Preload images */
body:after {
  content: url(../../img/close.png) url(../../img/loading.gif) url(../../img/prevl.png) url(../../img/nextl.png);
  display: none;
}

body.lb-disable-scrolling {
  overflow: hidden;
}

.lightboxOverlay {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9999;
  background-color: black;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=80);
  opacity: 0.8;
  display: none;
}

.lightbox {
  position: absolute;
  left: 0;
  width: 100%;
  z-index: 10000;
  text-align: center;
  line-height: 0;
  font-weight: normal;
}

.lightbox .lb-image {
  display: block;
  height: auto;
  max-width: inherit;
  max-height: none;
  border-radius: 3px;
  /* Image border */
  border: 4px solid white;
}

.lightbox a img {
  border: none;
}

.lb-outerContainer {
  position: relative;
  *zoom: 1;
  width: 250px;
  height: 250px;
  margin: 0 auto;
  border-radius: 4px;
  /* Background color behind image
   * This is visible during transitions.*/
  background-color: white;
}

.lb-outerContainer:after {
  content: "";
  display: table;
  clear: both;
}

.lb-loader {
  position: absolute;
  top: 43%;
  left: 0;
  height: 25%;
  width: 100%;
  text-align: center;
  line-height: 0;
}

.lb-cancel {
  display: block;
  width: 32px;
  height: 32px;
  margin: 0 auto;
  background: url(../../img/loading.gif) no-repeat;
}

.lb-nav {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 10;
}

.lb-container > .nav {
  left: 0;
}

.lb-nav a {
  outline: none;
  background-image: url("data:image/gif;base64,R0lGODlhAQABAPAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw==");
}

.lb-prev, .lb-next {
  height: 100%;
  cursor: pointer;
  display: block;
}

.lb-nav a.lb-prev {
  width: 34%;
  left: 0;
  float: left;
  background: url(../../img/prevl.png) left 48% no-repeat;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
  opacity: 0;
  -webkit-transition: opacity 0.6s;
  -moz-transition: opacity 0.6s;
  -o-transition: opacity 0.6s;
  transition: opacity 0.6s;
}

.lb-nav a.lb-prev:hover {
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
  opacity: 1;
}

.lb-nav a.lb-next {
  width: 64%;
  right: 0;
  float: right;
  background: url(../../img/nextl.png) right 48% no-repeat;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
  opacity: 0;
  -webkit-transition: opacity 0.6s;
  -moz-transition: opacity 0.6s;
  -o-transition: opacity 0.6s;
  transition: opacity 0.6s;
}

.lb-nav a.lb-next:hover {
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
  opacity: 1;
}

.lb-dataContainer {
  margin: 0 auto;
  padding-top: 5px;
  *zoom: 1;
  width: 100%;
  -moz-border-radius-bottomleft: 4px;
  -webkit-border-bottom-left-radius: 4px;
  border-bottom-left-radius: 4px;
  -moz-border-radius-bottomright: 4px;
  -webkit-border-bottom-right-radius: 4px;
  border-bottom-right-radius: 4px;
}

.lb-dataContainer:after {
  content: "";
  display: table;
  clear: both;
}

.lb-data {
  padding: 0 4px;
  color: #ccc;
}

.lb-data .lb-details {
  width: 85%;
  float: left;
  text-align: left;
  line-height: 1.1em;
}

.lb-data .lb-caption {
  font-size: 13px;
  font-weight: bold;
  line-height: 1em;
}

.lb-data .lb-caption a {
  color: #4ae;
}

.lb-data .lb-number {
  display: block;
  clear: left;
  padding-bottom: 1em;
  font-size: 12px;
  color: #999999;
}

.lb-data .lb-close {
  display: block;
  float: right;
  width: 30px;
  height: 30px;
  background: url(../../img/close.png) top right no-repeat;
  text-align: right;
  outline: none;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=70);
  opacity: 0.7;
  -webkit-transition: opacity 0.2s;
  -moz-transition: opacity 0.2s;
  -o-transition: opacity 0.2s;
  transition: opacity 0.2s;
}

.lb-data .lb-close:hover {
  cursor: pointer;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
  opacity: 1;
}
